import * as React from "react";
import styled from "styled-components";

import { Cell } from "./Cell";

const LogoTitle = styled.h1`
    margin-bottom: 10px;
    font-size: 2rem;
    font-weight: bold;
    line-height: 2rem;
    letter-spacing: 1px;
`;

const LogoSubtitle = styled.div`
    line-height: 1rem;
`;

export default class Header extends React.PureComponent {
    render() {
        return (
            <Cell>
                <a href="/">
                    <LogoTitle>
                        Aleksey Fedotov
                    </LogoTitle>

                    <LogoSubtitle>
                        Full-time software engineer and occasional spare-time game developer.
                    </LogoSubtitle>
                </a>
            </Cell>
        )
    }
}
